import React, { useState, useLayoutEffect } from "react";
import { useLocation } from "react-router-dom";
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
} from "reactstrap";
import { NavLink as Link } from "react-router-dom";
import logo from "./assets/Quresoft_Logo.png";

const DefaultNavbar = () => {
  const [isOpen, setIsOpen] = useState(false);

  const toggle = () => setIsOpen(!isOpen);

  useLayoutEffect(() => {
    window.scrollTo(0, 0);
  });

  const currentPath = useLocation();

  window.console.log(currentPath);

  return (
    <div className="stickyNav bgForNav w-100">
      {/* <Container fluid> */}
      <Navbar expand="md" className="App-logo">
        <NavbarBrand tag={Link} to="/home">
          <img src={logo} alt="logo" width="200" height="100" />
        </NavbarBrand>
        <NavbarToggler onClick={toggle} />
        <Collapse isOpen={isOpen} navbar className="">
          <Nav className="" navbar style={{ marginLeft: "10%" }}>
            <NavItem>
              <NavLink tag={Link} to="/home" exact>
                Home
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                tag={NavLink}
                href="/#aboutUs"
                active={currentPath?.hash?.includes("#aboutUs") ? true : false}
              >
                About us
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                tag={NavLink}
                href="/#products"
                active={currentPath?.hash?.includes("#products") ? true : false}
              >
                Our Products
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink tag={Link} to="/contact-us" exact>
                Contact Us
              </NavLink>
            </NavItem>
          </Nav>
        </Collapse>
      </Navbar>
      {/* </Container> */}
    </div>
  );
};

export default DefaultNavbar;
